import mongoose from 'mongoose';
mongoose.Promise = global.Promise;

const Schema = mongoose.Schema;

const AgentTransactionSchema = mongoose.Schema({
	superAgent: {
		type: String,
		required: true
	},
	agent: {
		type: String
	},
	user: {
		type: String
	},
	completedAt: {
		type: Date,
		required: true
	},
	description: {
		type: String,
	},
	orderNumber: {
		type: String,
		required: true
	},
	amount: {
		type: Number,
		required: true
	},
	resultAmount:{
		type: Number,
		required: true
	},
	status: {
		type: String,
		required: true
	}
})

const AgentTransaction = mongoose.model('transaction', AgentTransactionSchema);

export default AgentTransaction;